.programs-list-wrapper-placeholder {
  height: 162px;
  overflow-y: hidden;

  .program-list-skeleton {
    width: 100%;
    height: 64px;
    overflow: hidden;
    position: relative;
    margin-bottom: 4px;
    border-radius: 8px;

    &::after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      transform: translateX(-100%);
      animation: shimmer 1s infinite;
      content: '';
    }

    @keyframes shimmer {
      100% {
        transform: translateX(100%);
      }
    }
  }
}
